<template>
  <div class="app-container">
    <el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="120px">
      <el-form-item label="" prop="factoryCode">
        <el-input name="factoryCode" type="text" v-model="ruleForm.factoryCode" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="factoryName">
        <el-input name="factoryName" type="text" v-model="ruleForm.factoryName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="factoryOwner">
        <el-input name="factoryOwner" type="text" v-model="ruleForm.factoryOwner" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="factoryAddress">
        <el-input name="factoryAddress" type="text" v-model="ruleForm.factoryAddress" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="sensoryIndex">
        <el-input name="sensoryIndex" type="text" v-model="ruleForm.sensoryIndex" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="manageSysAuth">
        <el-input name="manageSysAuth" type="text" v-model="ruleForm.manageSysAuth" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="file">
        <input type="file" ref="imgFile" id="imgFile" @change="changeHandle">
      </el-form-item>
      <el-form-item label="">
        <el-col :span="6">
          <el-form-item label="X">
            <el-input name="xAxis" type="number" v-model="ruleForm.xAxis" placeholder="X"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6" :offset="2">
          <el-form-item label="Y">
            <el-input name="yAxis" type="number" v-model="ruleForm.yAxis" placeholder="Y"></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit('ruleForm')"></el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { modifyFactory, queryFactory } from '@/api/factory'
import { ERR_OK } from '@/api/config'
import { Message } from 'element-ui'

export default {
  data () {
    return {
      rules: {
        factoryCode: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        factoryName: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        factoryOwner: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        factoryAddress: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        sensoryIndex: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        manageSysAuth: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      },
      ruleForm: {
        id: this.$route.params.id,
        factoryCode: '',
        factoryName: '',
        factoryOwner: '',
        factoryAddress: '',
        sensoryIndex: '',
        manageSysAuth: '',
        xAxis: '',
        yAxis: '',
        image: ''
      },
      file: ''
    }
  },
  created () {
    if (this.ruleForm.id === 0) {
      this.$router.push({
        path: '/axm/factory/list'
      })
    } else {
      this.queryFactory()
    }
  },
  methods: {
    // 
    onSubmit (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.edit()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    async  queryFactory () {
      let data = {
        id: this.ruleForm.id
      }
      let res = await queryFactory(data)
      if (res.data.code === ERR_OK) {
        this.ruleForm = res.data
        return false
      }
      Message(res.data.msg)
    },
    async edit () {
      let fileForm = new FormData()
      fileForm.append('id', this.ruleForm.id)
      fileForm.append('factoryCode', this.ruleForm.factoryCode)
      fileForm.append('factoryName', this.ruleForm.factoryName)
      fileForm.append('factoryOwner', this.ruleForm.factoryOwner)
      fileForm.append('factoryAddress', this.ruleForm.factoryAddress)
      fileForm.append('sensoryIndex', this.ruleForm.sensoryIndex)
      fileForm.append('manageSysAuth', this.ruleForm.manageSysAuth)
      fileForm.append('xAxis', this.ruleForm.xAxis)
      fileForm.append('yAxis', this.ruleForm.yAxis)
      fileForm.append('image', this.file)

      console.log(fileForm)
      let res = await modifyFactory(fileForm)
      console.log(res)
      if (res.data.code === ERR_OK) {
        this.$router.push({
          path: '/axm/factory/list'
        })
        return false
      }
      Message(res.data.msg)
    },
    // 
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    changeHandle (e) {
      this.file = e.target.files[0]
    }
  }
}
</script>


